import  React from "react";

class Relative extends React.Component {
 constructor(props){
     super(props);
     this.state={
         items: ""
     }
 }   
    render(){
    return(
    <div className="list-group">
        {
            console.log(this.props.movies)
        }
        <ul className="list-group">
            {
                this.props.movies.map(movies=>{
                    return <li key={movies} className="list-group-item">{movies.title}
                    <button onClick={this.props.delete}>delete</button>
                    </li>
                })
            }
            {/* <li className="list-group-item">Arjun Reddy</li>
            <li className="list-group-item">Kabhir Singh</li>
            <li className="list-group-item">Aadhitiya Varma</li> */}
        </ul>
    </div>
    )
}
}

export default Relative;