import React from "react";
import Family from "./Family";
import Relative from "./Relative";
import axios from "axios"

// const movies = [];

class Jokers extends React.Component {
     constructor(props){
          super(props);
          this.state = {
               movies : [],
               loading: false
          };
     }

     componentDidMount =()=>{
          this.setState({
               loading: true
          })
          axios
          .get("http://localhost:8080/posts")
          .then(response => {
               setTimeout(
               this.setState({
                  movies: response.data,
                  loading: false
               })
               ,300)
          })
          .catch(error=> {
               console.log("Error", error);
          }
          );
     };

     upload = item => {
          const id = this.state.movies.length + 1;
          const record = { id: id, title: item}
          axios.post("http://localhost:8080/posts", record)
          .then(this.componentDidMount())
          this.setState({
              movies: [...this.state.movies, record]
          });
      };

     handleDelete = i => {
          alert("g")
     }

render(){
     let loading ="";
     if(this.state.loading) {
          loading= <p>Loading... Please wait!</p>
     }
  return (
    <div>
         <p>{this.state.name}</p>
      <Family upload = {this.upload}/>
      {loading}
      <Relative movies = {this.state.movies} delete = {this.state.handleDelete}  />
    </div>
  );
};
}
export default Jokers;